"use client"

import { useState, useEffect } from "react"
import axios from "axios"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { AlertCircle, CheckCircle, RefreshCw } from "lucide-react"

export default function DiagnosticoTokenPage() {
  const [token, setToken] = useState<string | null>(null)
  const [tokenValido, setTokenValido] = useState<boolean | null>(null)
  const [tokenInfo, setTokenInfo] = useState<any>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [testResults, setTestResults] = useState<Array<{ rota: string; status: string; mensagem: string }>>([])

  // Carregar token ao montar o componente
  useEffect(() => {
    const storedToken = localStorage.getItem("token")
    setToken(storedToken)

    if (storedToken) {
      try {
        // Decodificar o token (sem verificar a assinatura)
        const tokenParts = storedToken.split(".")
        if (tokenParts.length === 3) {
          const payload = JSON.parse(atob(tokenParts[1]))
          setTokenInfo(payload)
          setTokenValido(true)
        } else {
          setTokenValido(false)
          setError("Token não está no formato JWT válido (deve ter 3 partes separadas por pontos)")
        }
      } catch (err) {
        setTokenValido(false)
        setError("Erro ao decodificar token: " + (err instanceof Error ? err.message : String(err)))
      }
    } else {
      setTokenValido(false)
      setError("Token não encontrado no localStorage")
    }
  }, [])

  // Função para testar uma rota
  const testarRota = async (rota: string, metodo = "GET", dados: any = null) => {
    if (!token) {
      return { status: "erro", mensagem: "Token não disponível" }
    }

    try {
      const baseUrl = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8080/sistema-gestao"
      const url = `${baseUrl}${rota}`

      const config = {
        method: metodo,
        url,
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        data: dados,
      }

      const response = await axios(config)
      return {
        status: "sucesso",
        mensagem: `Status: ${response.status} ${response.statusText}`,
      }
    } catch (err: any) {
      return {
        status: "erro",
        mensagem: err.response ? `Erro ${err.response.status}: ${err.response.statusText}` : `Erro: ${err.message}`,
      }
    }
  }

  // Função para testar todas as rotas
  const testarRotas = async () => {
    setLoading(true)
    setTestResults([])

    const rotas = [
      { rota: "/auth/me", metodo: "GET" },
      { rota: "/auth/refresh", metodo: "GET" },
      { rota: "/usuarios", metodo: "GET" },
      {
        rota: "/usuarios",
        metodo: "POST",
        dados: {
          username: "teste_" + Date.now(),
          nome: "Usuário de Teste",
          senha: "senha123",
          cargo: "Tester",
          perfil: "Operador",
          status: "Ativo",
        },
      },
    ]

    const resultados = []

    for (const { rota, metodo, dados } of rotas) {
      const resultado = await testarRota(rota, metodo, dados)
      resultados.push({
        rota: `${metodo} ${rota}`,
        status: resultado.status,
        mensagem: resultado.mensagem,
      })
    }

    setTestResults(resultados)
    setLoading(false)
  }

  return (
    <div className="container mx-auto py-8">
      <h1 className="text-3xl font-bold mb-6">Diagnóstico de Token</h1>

      <div className="grid gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Status do Token</CardTitle>
            <CardDescription>Informações sobre o token armazenado</CardDescription>
          </CardHeader>
          <CardContent>
            {token ? (
              <div className="space-y-4">
                <div className="flex items-center gap-2">
                  <span className="font-semibold">Status:</span>
                  {tokenValido === true ? (
                    <span className="text-green-600 flex items-center">
                      <CheckCircle className="h-4 w-4 mr-1" /> Válido
                    </span>
                  ) : tokenValido === false ? (
                    <span className="text-red-600 flex items-center">
                      <AlertCircle className="h-4 w-4 mr-1" /> Inválido
                    </span>
                  ) : (
                    <span>Verificando...</span>
                  )}
                </div>

                <div>
                  <span className="font-semibold">Token:</span>
                  <div className="mt-1 p-2 bg-gray-100 rounded text-xs overflow-auto max-h-20">{token}</div>
                </div>

                {tokenInfo && (
                  <div>
                    <span className="font-semibold">Informações do Token:</span>
                    <pre className="mt-1 p-2 bg-gray-100 rounded text-xs overflow-auto max-h-60">
                      {JSON.stringify(tokenInfo, null, 2)}
                    </pre>
                  </div>
                )}

                {error && (
                  <Alert variant="destructive">
                    <AlertCircle className="h-4 w-4" />
                    <AlertDescription>{error}</AlertDescription>
                  </Alert>
                )}
              </div>
            ) : (
              <Alert variant="destructive">
                <AlertCircle className="h-4 w-4" />
                <AlertDescription>Token não encontrado no localStorage</AlertDescription>
              </Alert>
            )}
          </CardContent>
          <CardFooter>
            <Button
              onClick={() => {
                localStorage.removeItem("token")
                localStorage.removeItem("user")
                window.location.reload()
              }}
              variant="outline"
              className="mr-2"
            >
              Limpar Token
            </Button>
            <Button onClick={() => window.location.reload()}>Recarregar</Button>
          </CardFooter>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Teste de Rotas</CardTitle>
            <CardDescription>Teste as rotas da API com o token atual</CardDescription>
          </CardHeader>
          <CardContent>
            <Button onClick={testarRotas} disabled={loading || !token}>
              {loading ? (
                <>
                  <RefreshCw className="mr-2 h-4 w-4 animate-spin" /> Testando...
                </>
              ) : (
                "Testar Rotas"
              )}
            </Button>

            {testResults.length > 0 && (
              <div className="mt-4">
                <h3 className="text-lg font-semibold mb-2">Resultados:</h3>
                <div className="space-y-2">
                  {testResults.map((result, index) => (
                    <div
                      key={index}
                      className={`p-3 rounded ${result.status === "sucesso" ? "bg-green-100" : "bg-red-100"}`}
                    >
                      <div className="font-medium">{result.rota}</div>
                      <div className={`text-sm ${result.status === "sucesso" ? "text-green-700" : "text-red-700"}`}>
                        {result.mensagem}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
